import PropTypes from 'prop-types'
import {
    Button,
    Dialog,
    DialogContent,
    DialogTitle
} from "@material-ui/core";
import {useMemo, useState} from "react";
import {ChainId} from "@udegoswap/sdk";
import {ethers} from "ethers";
import InputField from "./InputField";
import useStakeFarms from "../hooks/useStakeFarms";
import useUnstakeFarms from "../hooks/useUnstakeFarms";
import {useLpTokenPrice} from "../../../state/farms/hooks";
import useActiveWeb3React from "../../../hooks/useWeb3";
import {useAppDispatch} from "../../../redux/store";
import {fetchFarmUserDataAsync} from "../../../state/farms";
import {showError} from "../../../config/helpers";
import {getFullDisplayBalance} from "../../../utils/formatBalance";

const StakeAction = ({
    farm,
    tokenBalance,
    tokenName,
    pid,
    lpLabel,
    isUnstake,
    isDisabled=false
}) => {

    const [open, setOpen] = useState(false)
    const [input, setInput] = useState(false)
    const { onStake } = useStakeFarms(pid)
    const { onUnstake } = useUnstakeFarms(pid)
    const { account } = useActiveWeb3React()
    const dispatch = useAppDispatch()
    const lpPrice = useLpTokenPrice(tokenName)

    const fullBalance = useMemo(() => getFullDisplayBalance(tokenBalance), [tokenBalance])

    const handleClose = () => {
        setOpen(false)
    }

    const openDialog = () => {
        setInput("")
        setOpen(true)
    }

    const handleInput = (val) => {
        setInput(val)
    }

    const handleMax = () => {
        setInput(fullBalance)
    }

    const handleStake = async (amount) => {
        try {
            await onStake(amount)
            dispatch(fetchFarmUserDataAsync({account, pids: [pid]}))
        }catch (e) {
            showError(e.data ? e.data.message : e.message)
            return null
        }
    }

    const handleUnstake = async (amount) => {
        try{
            await onUnstake(amount)
            dispatch(fetchFarmUserDataAsync({ account, pids: [pid] }))
        }catch (e) {
            showError(e.data ? e.data.message : e.message)
            return null
        }
    }

    const onDismiss = () => {
        setOpen(false)
    }

    return (
        <>
            <button
                className="px-4 py-3 text-center text-sm text-white bg-sky-600 rounded-lg font-medium hover:bg-blue-600 transition-all w-full md:w-auto xs:w-auto disabled:bg-opacity-40"
                variant="contained"
                color="primary"
                disabled={isDisabled}
                onClick={openDialog}
            >
                {
                    isUnstake ?
                        "Unstake"
                        : "Stake"
                }
            </button>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle>
                    {
                        isUnstake ?
                            "Unstake"
                            : "Stake"
                    } {lpLabel}
                </DialogTitle>
                <DialogContent dividers>
                    <InputField
                        tokenSymbol={farm.lpSymbol}
                        selectedToken={farm.lpAddresses[ChainId.MAINNET]}
                        tokenBalance={tokenBalance}
                        onInputChange={handleInput}
                        inputValue={input}
                        max={tokenBalance}
                        onMax={handleMax}
                        onConfirm={isUnstake ? handleUnstake : handleStake}
                        onDismiss={onDismiss}
                    />
                </DialogContent>
            </Dialog>
        </>
    )
}

StakeAction.propTypes = {
    farm: PropTypes.any,
    tokenBalance: PropTypes.any,
    stakedBalance: PropTypes.any,
    tokenName: PropTypes.any,
    pid: PropTypes.any,
    lpLabel: PropTypes.any,
    isUnstake: PropTypes.bool,
    isDisabled: PropTypes.bool
}

export default StakeAction

