import BigNumber from 'bignumber.js'
import getGasPrice from '../getGasPrice'
import { DEFAULT_GAS_LIMIT, DEFAULT_TOKEN_DECIMAL } from '../../config/index'
import {showError, showPromise} from "../../config/helpers";

export const stakeFarm = async (masterChefContract, pid, amount, isCompound) => {
    const gasPrice = getGasPrice()
    const value = new BigNumber(amount).times(DEFAULT_TOKEN_DECIMAL).toFormat().replaceAll(",", "")
    try{
        if (pid === 0) {
            const tx = await masterChefContract.enterStaking(value)
            showPromise(tx.wait())
            const receipt = await tx.wait()
            return receipt.status
        }

        const tx = await masterChefContract.deposit(pid, value)
        showPromise(tx.wait())
        const receipt = await tx.wait()
        return receipt.status
    }catch (e) {
        showError(e.data ? e.data.message : e.message)
        console.log(e)
        return null
    }
}

export const unstakeFarm = async (masterChefContract, pid, amount) => {
    // const gasPrice = getGasPrice()
    const value = new BigNumber(amount).times(DEFAULT_TOKEN_DECIMAL).toFormat().replaceAll(",", "")
    try {
        if (pid === 0) {
            const tx = await masterChefContract.leaveStaking(value)
            showPromise(tx.wait())
            const receipt = await tx.wait()
            return receipt.status
        }

        const tx = await masterChefContract.withdraw(pid, value)
        showPromise(tx.wait())
        const receipt = await tx.wait()
        return receipt.status
    }catch (e) {
        showError(e.data ? e.data.message : e.message)
        return null
    }
}

export const harvestFarm = async (masterChefContract, pid) => {
    const gasPrice = getGasPrice()
    try {
        if (pid === 0) {
            const tx = await masterChefContract.enterStaking('0')
            showPromise(tx.wait())
            const receipt = await tx.wait()
            return receipt.status
        }

        const tx = await masterChefContract.deposit(pid, '0')
        showPromise(tx.wait())
        const receipt = await tx.wait()
        return receipt.status
    }catch (e) {
        showError(e.data ? e.data.message : e.message)
        return null
    }
}
