import PropTypes from 'prop-types'
import {
    Button,
} from "@material-ui/core";
import {useState} from "react";
import BigNumber from "bignumber.js/bignumber";
import { usePriceUdeBusd} from "../../../state/farms/hooks";
import useActiveWeb3React from "../../../hooks/useWeb3";
import {useAppDispatch} from "../../../redux/store";
import useHarvestFarm from "../hooks/useHarvestFarm";
import {getBalanceAmount} from "../../../utils/formatBalance";
import {BIG_ZERO} from "../../../utils/bigNumber";
import {fetchFarmUserDataAsync} from "../../../state/farms";
import {showError, showFulfill} from "../../../config/helpers";

const HarvestAction = ({
    earnings,
    pid,
    timeStamp=0
 }) => {
    const { account } = useActiveWeb3React()
    const dispatch = useAppDispatch()

    const [pendingTx, setPendingTx] = useState(false)
    const { onReward } = useHarvestFarm(pid)
    const rawEarningsBalance = account ? getBalanceAmount(earnings) : BIG_ZERO

    return (
        <>
            <button className="px-4 py-3 text-center text-sm text-white bg-sky-600 rounded-lg font-medium hover:bg-blue-600 transition-all w-full md:w-auto xs:w-auto disabled:bg-opacity-40"
                disabled={(parseFloat(timeStamp) > 0) || (rawEarningsBalance.eq(0) || pendingTx)}
                variant="contained"
                color="primary"
                onClick={async () => {
                    setPendingTx(true)
                    try{
                        await onReward()
                        showFulfill("Successfully Harvested UDE")
                    }catch (e) {
                        showError(e.data ? e.data.message : e.message)
                    }
                    dispatch(fetchFarmUserDataAsync({ account, pids: [pid] }))
                }}
            >
                Harvest
            </button>
        </>
    )
}

HarvestAction.propTypes = {
    earnings: PropTypes.any,
    pid: PropTypes.any,
    timeStamp: PropTypes.number
}

export default HarvestAction

