import PropTypes from 'prop-types'
import {Button, Divider, Grid, Typography} from "@material-ui/core";
import React, {useCallback, useEffect, useState} from "react";
import {getAddress} from "../../../utils/addressHelpers";
import {useAppDispatch} from "../../../redux/store";
import {useERC20} from "../../../hooks/useContract";
import useApproveFarm from "../hooks/useApproveFarm";
import {fetchFarmUserDataAsync} from "../../../state/farms";
import StakeAction from "./StakeAction";
import HarvestAction from "./HarvestAction";
import {formatUnit} from "../../../config/index";
import ConnectWallet from "../../../components/mtpl/ConnectWallet";

function InfoItem(number, label="") {
    return (
        <Grid item xs={4}>
            <div variant="caption" style={{ mb: 0.5, color: 'text.white', display: 'block', fontSize: '12px' }}>
                {label}
            </div>
            <span variant="subtitle1" className='text-xs'>{number}</span>
        </Grid>
    );
}

function formatTime(timeStmp) {
    var days = Math.floor(timeStmp / (1000 * 60 * 60 * 24));
    var hours = Math.floor((timeStmp % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    var minutes = Math.floor((timeStmp % (1000 * 60 * 60)) / (1000 * 60));
    // var seconds = Math.floor((timeStmp % (1000 * 60)) / 1000);

    let formt = days + "d " + hours + "h " + minutes + "m";
    return formt;
}

const FarmActions = ({
    farm,
    account,
    lpLabel,
    addLiquidityUrl,
    udePrice
    }) => {

    const [requestedApproval, setRequestedApproval] = useState(false)
    const { pid, lpAddresses } = farm
    const { allowance, tokenBalance, stakedBalance, earnings, userLast } = farm.userData || {}
    const lpAddress = getAddress(lpAddresses)
    const isApproved = account && allowance && allowance.isGreaterThan(0)
    const dispatch = useAppDispatch()

    const lpContract = useERC20(lpAddress)

    const { onApprove } = useApproveFarm(lpContract)

    const handleApprove = useCallback(async () => {
        try {
            dispatch(fetchFarmUserDataAsync({ account, pids: [pid] }))
            setRequestedApproval(true)
            await onApprove()
            dispatch(fetchFarmUserDataAsync({ account, pids: [pid] }))
            setRequestedApproval(false)
        } catch (e) {
            console.error(e)
        }
    }, [onApprove, dispatch, account, pid])

    const withdraw = 604800
    const investWithdraw = (parseFloat(userLast) + parseFloat(withdraw)) *  1000
    const nowStamp = Date.now()

    const differ = investWithdraw - nowStamp;
    const timeSt = ((differ/1000) > 0 ? (differ/1000) : 0)
    const hours = formatTime(timeSt * 1000)

    const renderApprovalOrStakeButton = () => isApproved ? (
            <StakeAction
                farm={farm}
                tokenBalance={tokenBalance}
                tokenName={farm.lpSymbol}
                pid={pid}
                lpLabel={lpLabel}
            />
        ) : (
            <button
                className="px-4 py-3 text-center text-sm text-white bg-blue-700 rounded-lg w-full font-medium hover:bg-sky-600 transition-all"
                variant="contained" disabled={requestedApproval} onClick={handleApprove}>
                Approve
            </button>
        )

    useEffect(
        () => dispatch(fetchFarmUserDataAsync({ account, pids: [pid] })),
        [dispatch]
    )

    return (
        <>
            {/* <Grid container sx={{ py: 3, textAlign: 'center' }} spacing={3}>
                <Grid item xs={4} md={4}>
                    <HarvestAction pid={pid} earnings={earnings} timeStamp={hours} />
                </Grid>
                <Grid item xs={4} md={4}>
                    {!account ? <ConnectWallet mt="8px" width="100%" /> : renderApprovalOrStakeButton()}
                </Grid>
                <Grid item xs={4} md={4}>
                    <StakeAction
                        farm={farm}
                        pid={pid}
                        tokenBalance={stakedBalance}
                        lpLabel={lpLabel}
                        tokenName={farm.lpSymbol}
                        isUnstake
                        isDisabled={!isApproved || parseFloat(stakedBalance) <= 0}
                    />
                </Grid>
            </Grid> */}

            <div className='flex items-center sm:flex-row flex-col justify-between gap-4 pb-5 mb-2 border-b border-white border-opacity-10'>
                <div className='sm:col-span-1 w-full md:w-auto xs:w-auto'>
                    <HarvestAction pid={pid} earnings={earnings} />
                </div>
                <div className='sm:col-span-1 w-full md:w-auto xs:w-auto'>
                    {!account ? <ConnectWallet mt="8px" width="100%" /> : renderApprovalOrStakeButton()}
                </div>
                <div className='sm:col-span-1 w-full md:w-auto xs:w-auto'>
                    <StakeAction
                        farm={farm}
                        pid={pid}
                        tokenBalance={stakedBalance}
                        lpLabel={lpLabel}
                        tokenName={farm.lpSymbol}
                        isUnstake
                        isDisabled={!isApproved || parseFloat(stakedBalance) <= 0}
                    />
                </div>
            </div>

            

            <Grid container sx={{ pt: 2, textAlign: 'center' }}>
                {InfoItem(formatUnit(stakedBalance.toNumber()), "STAKED")}
                {InfoItem(formatUnit(earnings.toNumber(), 6), "REWARDS")}
                {InfoItem(`${hours}`, "LOCKED")}
            </Grid>
        </>
    )
}

FarmActions.propTypes = {
    farm: PropTypes.any
}

export default FarmActions